
.file {
  display: inline-block;
  overflow: hidden;
  vertical-align: middle;
  border: none;
  cursor: pointer;
}
.file input {
  position: absolute;
  top: 0;
  left: -100%;
  visibility: hidden;
}
.file button {
  float: left;
  border: 2px solid var(--fg-color);
}
.file button:hover {
  border-color: var(--active-fg-color);
}
.file input,
.file button {
  margin: 0;
}


